import React from "react";
import "./Projects.css";

import { Link } from "react-router-dom";
import { ImGithub } from "react-icons/im";
import { FaEye } from "react-icons/fa";


const Projects = () => {
  return (
    <>
      <section className="projectIntro">
        <h2>My Projects</h2>
        <p>
          Here are some projects that I created. Two of the them are fun
          experiments with CSS. The other two are my first React projects! One
          is a weather app that provides the weather for two cities, Montreal
          and Amsterdam. The second one is my first webshop built with React
          and Redux. It 'sells' eggwarmers. More to come in the future...
        </p>
      </section>
      <section className="projectDisplay">
        <div className="projectScreen">
        
          <div className="projectText">
          <h2>Fun with CSS</h2>
            <p>Animated Neon Letters</p>
            <p></p>
            <a href="https://github.com/LeontineC/Projects/tree/main/Neon"><ImGithub className='linksProjectGithub'/></a>
            <Link to="/neon" className="linksProject">
              <FaEye />
            </Link>
          </div>
        </div>
        <div className="projectScreen">
       
          <div className="projectText">
          <h2>React/ API</h2>
            <p>Weather App</p>
            <p></p>
            <a href="https://github.com/LeontineC/weather-app"><ImGithub className='linksProjectGithub'/></a>
            <a
              href="https://leontinec.github.io/weather-app/"
              className="linksProject"
            >
              <FaEye />
            </a>
          </div>
        </div>
        <div className="projectScreen">
        
          <div className="projectText">
          <h2>Fun with CSS</h2>
            <p>Animated Particles</p>
            <p></p>
            <a href="https://github.com/LeontineC/Projects/tree/main/Particles"><ImGithub className='linksProjectGithub'/></a>
            <Link to="/particles" className="linksProject">
            <FaEye />
            </Link>
          </div>
        </div>
        <div className="projectScreen">
        
          <div className="projectText">
          <h2>React/ Redux</h2>
            <p>Webshop</p>
            <p></p>
            <a href="https://github.com/LeontineC/webshop"><ImGithub className='linksProjectGithub'/></a>
            <a
              href="https://leontinec.github.io/webshop/"
              className="linksProject"
            >
              <FaEye />
            </a>
          </div>
        </div>
      </section>
    </>
  );
};

export default Projects;
